export default {
  legend: {
    labels: {
      boxWidth: 12,
    },
  },
  maintainAspectRatio: false, // 
  scales: {
    xAxes: [{
      gridLines: {
        display: false,
      },
      scaleLabel: {
        display: true,
        labelString: ' ( 24  )',
        fontSize: 12,
      },
      ticks: {
        callback: (value) => value,
        // stepSize: 10,
      },
    }],
    yAxes: [{
      gridLines: {
        display: true,
      },
      scaleLabel: {
        display: true,
        labelString: '',
        fontSize: 12,
      },
      ticks: {
        min: 0,
        // stepSize: 20,
      },
    }],
  },
  tooltips: {
    mode: 'index',
    // intersect: false,
    callbacks: {
      title: (tooltipItem, data) => ` ${data.labels[tooltipItem[0].index]}00`,
      label: (tooltipItem, data) => {
        if (Number(tooltipItem.value)) {
          return `${data.datasets[tooltipItem.datasetIndex].label} : ${tooltipItem.value} `;
        }
        return '';
      },
    },
  },
  layout: {
    padding: {
      right: 30,
    },
  },
};
