export default {
  legend: {
    onClick: (e) => e.stopPropagation(), //  labels 
    labels: {
      boxWidth: 13,
    },
  },
  maintainAspectRatio: false, // 
  hover: {
    animationDuration: 0, // 
  },
  animation: {
    duration: 500, // hover
    onComplete() {
      const Chart = this.chart;
      let { ctx } = Chart;
      const chartInstance = this.chart;
      ctx = chartInstance.ctx;
      ctx.fillStyle = '#ffffff';
      ctx.textAlign = 'center';
      ctx.textBaseline = 'middle';

      this.data.datasets.forEach((dataset, i) => {
        const meta = chartInstance.controller.getDatasetMeta(i);
        meta.data.forEach((bar, index) => {
          const data = dataset.data[index];
          const centerPoint = bar.getCenterPoint(); // x.y
          if (data !== 0 && bar.height() > 12) { // bar.height() 
            ctx.fillText(data, centerPoint.x, centerPoint.y);
          }
        });
      });
    },
  },
  scales: {
    xAxes: [{
      stacked: true,
      gridLines: {
        display: false,
      },
      scaleLabel: {
        display: true,
        labelString: '',
        fontSize: 12,
        padding: {
          top: 30,
          bottom: 10,
        },
      },
      ticks: {
        callback: (value) => value,
        // stepSize: 10,
      },
    }],
    yAxes: [{
      stacked: true,
      scaleLabel: {
        display: true,
        labelString: '',
        fontSize: 12,
      },
      ticks: {
        min: 0,
        // stepSize: 10,
      },
    }],
  },
  tooltips: {
    callbacks: {
      title: (tooltipItem, data) => `${data.labels[tooltipItem[0].index]} `,
      label: (tooltipItem, data) => `${data.datasets[tooltipItem.datasetIndex].label} : ${tooltipItem.value}`,
    },
  },
  layout: {
    padding: {
      top: 50,
      bottom: 20,
      left: 20,
      right: 20,
    },
  },
};
